import React from "react";
import Book from "./Book";

const BookList = ({ json }) => {
  return (
    <div className="row">
      {json.map((items, i) => {
        if (json[i].saleInfo.saleability === "NOT_FOR_SALE") {
          var salePrice = "This is not for sale";
          var classBadge = "badge badge-secondary";
        } else {
          var salePrice = Number(json[i].saleInfo.retailPrice.amount).toFixed(
            2
          );
          var currency = "Price: " + json[i].saleInfo.retailPrice.currencyCode;
          var classBadge = "badge badge-primary";
        }
        return (
          <Book
            key={json[i].id}
            id={json[i].id}
            volumeInfo={json[i].volumeInfo.title}
            thumb={json[i].volumeInfo.imageLinks.thumbnail}
            saleInfo={json[i].saleInfo.saleability}
            price={salePrice}
            currency={currency}
            badge={classBadge}
          />
        );
      })}
    </div>
  );
};
export default BookList;
