import React, { Component } from "react";
import BookList from "../components/BookList";
import Searchbox from "../components/Searchbox";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      json: [],
      searchfield: "",
      isLoaded: false
    };
  }
  componentDidMount() {
    fetch("https://www.googleapis.com/books/v1/volumes?q=harry%20potter")
      .then(res => res.json())
      .then(data => {
        // Here you need to use an temporary array to store item only
        let tmpArray = [];
        for (var i = 0; i < data.items.length; i++) {
          tmpArray.push(data.items[i]);
        }
        this.setState({
          json: tmpArray,
          isLoaded: true
        });
      });
  }
  onSearchChange = event => {
    this.setState({ searchfield: event.target.value });
  };
  render() {
    const { isLoaded, json, searchfield } = this.state;
    const filteredBooks = json.filter(item => {
      return item.volumeInfo.title
        .toLowerCase()
        .includes(searchfield.toLowerCase());
    });
    if (!isLoaded) {
      return (
        <main className="container">
          <h1>Loading...</h1>
        </main>
      );
    } else {
      return (
        <main className="container">
          <h1 className="text-center">Harry Potter books</h1>
          <Searchbox searchChange={this.onSearchChange} />
          <BookList json={filteredBooks}></BookList>
        </main>
      );
    }
  }
}

export default App;
