import React from "react";

const Book = props => {
  const { id, volumeInfo, thumb, price, currency, badge } = props;
  return (
    <div className="col-12 col-md-6 col-lg-3" key={id}>
      <div className="card">
        <img className="card-img-top" src={thumb} alt={volumeInfo} />
        <div className="card-body">
          <h4 className="card-title">{volumeInfo}</h4>
          <span className={badge}>
            {currency}
            {price}
          </span>
        </div>
      </div>
    </div>
  );
};

export default Book;
