import React from "react";

const Searchbox = ({ searchChange }) => {
  return (
    <form className="form-inline row">
      <div className="col-lg-4"></div>
      <input
        className="form-control col-lg-4"
        type="search"
        placeholder="search"
        onChange={searchChange}
      />
    </form>
  );
};

export default Searchbox;
